/* eslint-disable vue/multi-word-component-names */
<template>
   <div class="login_container">
      <div class="login_box">
        <!-- 头像 -->
        <div class="avatar_box">
          <img src="../assets/logowl.png" alt="">
        </div>
        <!-- 登录表单区 -->
        <el-form
            ref="loginFormRef"
            :model="loginForm"
            :rules="loginFormRules"
            label-width="0px"
            class="login_form">
          <!-- 用户名 -->
          <el-form-item prop="username">
            <el-input
                prefix-icon="el-icon-user"
                v-model="loginForm.username"
            ></el-input>
              <!-- 密码 -->
          </el-form-item >
          <el-form-item prop="password">
            <el-input
                  prefix-icon="el-icon-lock"
                  v-model="loginForm.password"
                  type="password"
            ></el-input>
          </el-form-item>
          <!-- 按钮 -->
          <el-form-item class="btns">
            <el-button type="primary" @click="login">登录</el-button>
            <el-button type="info" @click="resetLoginForm">重置</el-button>
          </el-form-item>
        </el-form>
        <!-- <el-button type="info" @click="logout">退出</el-button> -->
      </div>
   </div>
</template>

<script>
// @ is an alias to /src
export default {
  name: 'Login',
  data () {
    return {
      // 登录表单的数据绑定对象
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 表单验证规则对象
      loginFormRules: {
        // 验证用户名是否合法
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 4, max: 6, message: '长度在4~6个字符', trigger: 'blur' }
        ],
        // 验证密码的合法性
        password: [
          { required: true, message: '请输入登登录密码', trigger: 'blur' },
          { min: 6, max: 15, message: '长度在 6~15 个字符', trigger: 'blur' }
        ]
      }

    }
  },
  methods: {
    // 点击重置按钮，重置登录表单
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    login () {
      this.$refs.loginFormRef.validate(async (valid) => {
        // console.log(valid)// true or false
        if (!valid) return
        const { data: result } = await this.$http.post('login', this.loginForm)
        if (result.meta.status !== 200) return this.$message.error('登录失败')
        this.$message.success('登录成功')
        // 将登录成功之后的token 保存到客户端的 sessionStorage 里（会话期间的存储机制，so不放在localStorage）
        console.log(result)
        window.sessionStorage.setItem('token', result.data.token)
        // 通过编程式导航跳转到后台主页，路由地址：/home
        this.$router.push('/home')
      })
    }
  }

}
</script>

<style scoped>
  .login_container{
    background-color: #409eff;
    height: 100%;
  }
  .login_box {
    width: 450px;
    height: 300px;
    background-color: #fff;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }
  .avatar_box {
    height: 130px;
    width: 130px;
    border: 1px solid #eee;
    border-radius: 50%;
    padding: 10px;
    box-shadow: 0 0 10px #ddd;
    position: absolute;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #fff;
  }
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #eee;
  }

  .login_form {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;
  }
  .btns {
    display: flex;
    justify-content: flex-end;
  }

</style>
